.news-slider__block {
  &>p:first-child {
    font-size: 1.125rem;

    @media (max-width: 550px) {
      font-size: 1rem;
      line-height: 1.5;
    }
  }

  &>p:nth-child(2) {
    @media (max-width: 550px) {
      font-size: 0.875rem;
    }
  }
}

.news-slider__date {
  color: rgba(6, 27, 49, 0.4);

  @media (max-width: 550px) {
    font-size: 0.75rem;
  }
}